export function allmap() {
  return new Promise((resolve) => {
    let map = new BMap.Map('allmap')
    let data = {}
    var geolocation = new BMap.Geolocation()
    geolocation.getCurrentPosition((r) => {
      data = {
        location: r.longitude + ',' + r.latitude,
        city: r.address.city, //获取城市信息
        province: r.address.province, //获取省份信息
        district: r.address.district, //县
        street: r.address.street, //街道
        LocationCity: r.address.province + r.address.city + r.address.district, //赋值
        newCity: r.address.city
      }

      let newCity = r.address.city
      if (newCity.substr(newCity.length - 1, newCity.length) == "市") {
        newCity = newCity.substr(0, newCity.length - 1);
      }
      let data = {
        city: r.address.city,
        newCity: newCity,
        LocationCity: r.address.province + r.address.city + r.address.district,
        location: JSON.stringify([r.longitude,r.latitude]),
      }
      window.sessionStorage.setItem('city', JSON.stringify(data))
      resolve(data)
    }, {
      enableHighAccuracy: true
    })
  })
}
