	} from '@/api/user.js'

	export default {
		components: {
			headers,
			footers,
			APlayer
		},
		data() {
			return {
				ap: Object,
				footersShow: true
			}
		},
		methods: {
			goTo() {
				this.$router.push({
					path: '/home'
				})
			},

		},
		mounted() {
			new Promise((resolve) => {
				musicQuery().then(res => {
					const ap = new APlayer({
						container: this.$refs.aplayer,
						fixed: true,
						autoplay: true,
						volume: 0.2,
						audio: res.list,
					});
					resolve(ap)
				})
			}).then((ap) => {
				let than = this;
				this.ap = ap
			})


		},
