import {
  IpAddress,
  InFormation
} from '@/api/user'
import {
  allmap
} from '@/utils/location.js'

allmap().then((data) => {
  IpAddress().then(res => {
    let city = window.sessionStorage.getItem('city');
    let ip = window.sessionStorage.getItem('ip');
    let data = {
      ip: res.ip,
      LocationCity: JSON.parse(city).LocationCity,
      newCity: JSON.parse(city).newCity,
      location: JSON.parse(city).location,
      sum: ip ? '0' : '1'
    }
    if (!ip) {
		console.log(211);
      InFormation(data)
    }
    window.sessionStorage.setItem('ip', res.ip)
  })

});
