import request from '@/utils/request'
import Qs from 'qs';
export function imgPage(data) {
	return request({
		url: 'query/img/imgPage',
		method: 'post',
		data
	})
}

export function musicQuery() {
	return request({
		url: "query/Music/all",
		method: "post"
	})
}

//  获取 ip 地址 
export function IpAddress(data){
  return request({
		url: "tool/ip",
		method: "get"
  })
}
//  存储 ip 信息 
export function InFormation(data){
  return request({
		url: "visitor/Information",
		method: "post",
    data
  })
}